<template>
  <div>
    <div class="preloader" style="opacity: 0; display: none">
      <div class="preloader-inner">
        <div class="preloader-icon"><span></span> <span></span></div>
      </div>
    </div>

    <div class="breadcrumbs">
      <div class="container">
        <div class="row align-items-center">
          <div class="col-lg-6 col-md-6 col-12">
            <div class="breadcrumbs-content">
              <h1 class="page-title">Bookmarked Area Informations</h1>
            </div>
          </div>
          <div class="col-lg-6 col-md-6 col-12">
            <ul class="breadcrumb-nav">
              <li><a href="https://demo.graygrids.com/themes/classigrids-demo/index.jsp">Home</a></li>
              <li>Bookmarked Area Informations</li>
            </ul>
          </div>
        </div>
      </div>
    </div>

    <section class="item-details section">
      <div class="container">
        <div class="mb-3">
          <h3>{{ bookmark.sidoName }} {{ bookmark.gugunName }} {{ bookmark.dongName }}</h3>
        </div>
        <div class="top-area">
          <div class="row">
            <div class="col-lg-12 col-md-12 col-12">
              <div class="product-images">
                <main id="gallery">
                  <!-- kakao map start -->
                  <div class="map_wrap" style="height: 500px">
                    <div id="map" style="width: 100%; height: 100%; position: relative; overflow: hidden"></div>
                    <ul id="category">
                      <li id="BK9" data-order="0"><span class="category_bg bank"></span> </li>
                      <li id="MT1" data-order="1"><span class="category_bg mart"></span> </li>
                      <li id="PM9" data-order="2"><span class="category_bg pharmacy"></span> </li>
                      <li id="OL7" data-order="3"><span class="category_bg oil"></span> </li>
                      <li id="CE7" data-order="4"><span class="category_bg cafe"></span> </li>
                      <li id="CS2" data-order="5"><span class="category_bg store"></span> </li>
                    </ul>
                  </div>
                  <!-- kakao map end -->
                </main>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import http from '@/api/http';

export default {
  data() {
    return {
      // bookmark: [],
      // markers: [],
      // currCategory: '',
      // marker: '',
      // ps: '',
      // map: null,

      bookmark: [],
      items: [],
      placeOverlay: [],
      contentNode: [],
      markers: [],
      currCategory: [],
      map: [],
      places: [],
      infowindow: [],
    };
  },
  created() {
    http.get(`bookmark/detail?id=${this.$route.params.id}`).then(({ data }) => {
      if (data.message == 'success') {
        this.bookmark = data.bookmark;
      }
    });
  },
  mounted() {
    // /* eslint-disable */
    // if (window.kakao && window.kakao.maps) {
    //   this.initMap();
    // } else {
    const script = document.createElement('script');
    /* global kakao */
    script.onload = () => kakao.maps.load(this.initMap);
    script.src = '//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=86fe11c6c628248e5e1d06dca421c602&libraries=services';
    document.head.appendChild(script);
    // }
  },
  methods: {
    initMap() {
      (this.placeOverlay = new kakao.maps.CustomOverlay({ zIndex: 1 })),
        (this.contentNode = document.createElement('div')), //    
        (this.markers = []), //   
        (this.currCategory = ''); //      
      /* 1 */
      var mapContainer = document.getElementById('map'),
        mapOption = {
          center: new kakao.maps.LatLng(33.450701, 126.570667), //  
          level: 6, //   
        };
      this.map = new kakao.maps.Map(mapContainer, mapOption); //  
      // this.map.setZoomable(false);
      /* 2 :  to */
      var geocoder = new kakao.maps.services.Geocoder(); // -  
      const self = this;
      //   
      var address = `${this.bookmark.sidoName} ${this.bookmark.gugunName} ${this.bookmark.dongName}`;
      geocoder.addressSearch(address, function (result, status) {
        //   
        if (status === kakao.maps.services.Status.OK) {
          var coords = new kakao.maps.LatLng(result[0].y, result[0].x);
          //     
          var marker = new kakao.maps.Marker({
            map: self.map,
            position: coords,
          });
          //     
          var infowindow = new kakao.maps.InfoWindow({
            content: '<div style="width:150px;text-align:center;padding:6px 0;">' + `${self.bookmark.sidoName} ${self.bookmark.gugunName} ${self.bookmark.dongName}` + '</div>',
          });
          infowindow.open(self.map, marker);
          //      
          self.map.setCenter(coords);
        }
      });
      this.places = new kakao.maps.services.Places(this.map); //   
      kakao.maps.event.addListener(this.map, 'idle', this.searchPlaces); // idle event 
      this.contentNode.classList.add('placeinfo_wrap');
      this.addEventHandle(this.contentNode, 'mousedown', kakao.maps.event.preventMap);
      this.addEventHandle(this.contentNode, 'touchstart', kakao.maps.event.preventMap);
      this.placeOverlay.setContent(this.contentNode);
      this.addCategoryClickEvent(); //  addEvent
    },
    //     
    addEventHandle(target, type, callback) {
      if (target.addEventListener) {
        target.addEventListener(type, callback);
      } else {
        target.attachEvent('on' + type, callback);
      }
    },
    //     
    addCategoryClickEvent() {
      var category = document.getElementById('category'),
        children = category.children;
      for (var i = 0; i < children.length; i++) {
        children[i].onclick = this.onClickCategory;
      }
    },
    //     
    onClickCategory(event) {
      var id = event.currentTarget.id,
        className = event.currentTarget.className;
      this.placeOverlay.setMap(null);
      if (className === 'on') {
        this.currCategory = '';
        this.changeCategoryClass();
        this.removeMarker();
      } else {
        this.currCategory = id;
        this.changeCategoryClass(this);
        this.searchPlaces();
      }
    },
    //      
    changeCategoryClass(el) {
      var category = document.getElementById('category'),
        children = category.children,
        i;
      for (i = 0; i < children.length; i++) {
        children[i].className = '';
      }
      if (el) {
        el.className = 'on';
      }
    },
    //  
    searchPlaces() {
      // if (!this.currCategory) {
      //   console.log('');
      //   return;
      // }
      //   
      this.placeOverlay.setMap(null);
      //     
      this.removeMarker();
      //    
      this.places.categorySearch(this.currCategory, this.placesSearchCB, { useMapBounds: true });
      console.log(this.places);
    },
    //  
    placesSearchCB(data, status) {
      if (status === kakao.maps.services.Status.OK) {
        //  
        this.displayPlaces(data);

        // items 
        this.items = data;
      } else if (status === kakao.maps.services.Status.ZERO_RESULT) {
        //  
      } else if (status === kakao.maps.services.Status.ERROR) {
        //      
      }
    },
    //    
    displayPlaces(places) {
      console.log(places);
      //     
      //       
      var order = document.getElementById(this.currCategory).getAttribute('data-order');
      for (var i = 0; i < places.length; i++) {
        //    
        var marker = this.addMarker(new kakao.maps.LatLng(places[i].y, places[i].x), order);
        //      
        //     
        var self = this;
        (function (marker, place) {
          kakao.maps.event.addListener(
            marker,
            'click',
            function () {
              self.displayplaceinfo(place);
            },
            false
          );
        })(marker, places[i]);
      }
    },
    //         
    displayplaceinfo(place) {
      console.log('displayPlaceInfo');
      var content = '<div class="placeinfo">' + '   <a class="title" href="' + place.place_url + '" target="_blank" title="' + place.place_name + '">' + place.place_name + '</a>';
      if (place.road_address_name) {
        content +=
          '    <span title="' +
          place.road_address_name +
          '">' +
          place.road_address_name +
          '</span>' +
          '  <span class="jibun" title="' +
          place.address_name +
          '">( : ' +
          place.address_name +
          ')</span>';
      } else {
        content += '    <span title="' + place.address_name + '">' + place.address_name + '</span>';
      }
      content += '    <span class="tel">' + place.phone + '</span>' + '</div>' + '<div class="after"></div>';
      this.contentNode.innerHTML = content;
      this.placeOverlay.setPosition(new kakao.maps.LatLng(place.y, place.x));
      this.placeOverlay.setMap(this.map);
    },
    //       
    addMarker(position, order) {
      console.log('addMarker');
      var imageSrc = 'https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/places_category.png', //   url,   
        imageSize = new kakao.maps.Size(27, 28), //   
        imgOptions = {
          spriteSize: new kakao.maps.Size(72, 208), //   
          spriteOrigin: new kakao.maps.Point(46, order * 36), //       
          offset: new kakao.maps.Point(11, 28), //      
        },
        markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imgOptions),
        marker = new kakao.maps.Marker({
          position: position, //  
          image: markerImage,
        });
      marker.setMap(this.map); //    
      this.markers.push(marker); //    
      return marker;
    },
    //       
    removeMarker() {
      console.log('removeMarker');
      for (var i = 0; i < this.markers.length; i++) {
        this.markers[i].setMap(null);
      }
      this.markers = [];
    },
  },
};
</script>
<style>
.map_wrap,
.map_wrap * {
  margin: 0;
  padding: 0;
  font-family: 'Malgun Gothic', dotum, '', sans-serif;
  font-size: 12px;
}
.map_wrap {
  position: relative;
  width: 100%;
  height: 350px;
}
#category {
  position: absolute;
  top: 10px;
  left: 10px;
  border-radius: 5px;
  border: 1px solid #909090;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.4);
  background: #fff;
  overflow: hidden;
  z-index: 2;
}
#category li {
  float: left;
  list-style: none;
  width: 50px;
  border-right: 1px solid #acacac;
  padding: 6px 0;
  text-align: center;
  cursor: pointer;
}
#category li.on {
  background: #eee;
}
#category li:hover {
  background: #ffe6e6;
  border-left: 1px solid #acacac;
  margin-left: -1px;
}
#category li:last-child {
  margin-right: 0;
  border-right: 0;
}
#category li span {
  display: block;
  margin: 0 auto 3px;
  width: 27px;
  height: 28px;
}
#category li .category_bg {
  background: url(https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/places_category.png) no-repeat;
}
#category li .bank {
  background-position: -10px 0;
}
#category li .mart {
  background-position: -10px -36px;
}
#category li .pharmacy {
  background-position: -10px -72px;
}
#category li .oil {
  background-position: -10px -108px;
}
#category li .cafe {
  background-position: -10px -144px;
}
#category li .store {
  background-position: -10px -180px;
}
#category li.on .category_bg {
  background-position-x: -46px;
}
.placeinfo_wrap {
  position: absolute;
  bottom: 28px;
  left: -150px;
  width: 300px;
}
.placeinfo {
  position: relative;
  width: 100%;
  border-radius: 6px;
  border: 1px solid #ccc;
  border-bottom: 2px solid #ddd;
  padding-bottom: 10px;
  background: #fff;
}
.placeinfo:nth-of-type(n) {
  border: 0;
  box-shadow: 0px 1px 2px #888;
}
.placeinfo_wrap .after {
  content: '';
  position: relative;
  margin-left: -12px;
  left: 50%;
  width: 22px;
  height: 12px;
  background: url('https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/vertex_white.png');
}
.placeinfo a,
.placeinfo a:hover,
.placeinfo a:active {
  color: #fff;
  text-decoration: none;
}
.placeinfo a,
.placeinfo span {
  display: block;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.placeinfo span {
  margin: 5px 5px 0 5px;
  cursor: default;
  font-size: 13px;
}
.placeinfo .title {
  font-weight: bold;
  font-size: 14px;
  border-radius: 6px 6px 0 0;
  margin: -1px -1px 0 -1px;
  padding: 10px;
  color: #fff;
  background: #d95050;
  background: #d95050 url(https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/arrow_white.png) no-repeat right 14px center;
}
.placeinfo .tel {
  color: #0f7833;
}
.placeinfo .jibun {
  color: #999;
  font-size: 11px;
  margin-top: 0;
}
</style>
