<template>
  <div>
    <div class="preloader" style="opacity: 0; display: none">
      <div class="preloader-inner">
        <div class="preloader-icon"><span></span> <span></span></div>
      </div>
    </div>
    <!--   -->
    <div class="breadcrumbs">
      <div class="container">
        <div class="row align-items-center">
          <div class="col-lg-6 col-md-6 col-12">
            <div class="breadcrumbs-content">
              <h1 class="page-title"></h1>
            </div>
          </div>
          <div class="col-lg-6 col-md-6 col-12">
            <ul class="breadcrumb-nav">
              <li>
                <router-link class="active dd-menu collapsed" to="/" data-bs-toggle="collapse" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation"
                  >Home</router-link
                >
              </li>
              <li>Registration</li>
            </ul>
          </div>
        </div>
      </div>
    </div>

    <section class="login registration section">
      <div class="container">
        <div class="row">
          <div class="col-lg-6 offset-lg-3 col-md-8 offset-md-2 col-12">
            <div class="form-head">
              <h4 class="title"></h4>
              <form @submit="registHandler" id="formRegist">
                <div class="socila-login">
                  <ul>
                    <li>
                      <a href="javascript:void(0)" class="facebook"><i class="lni lni-facebook-original"></i>Import From Facebook</a>
                    </li>
                    <li>
                      <a href="javascript:void(0)" class="google"><i class="lni lni-google"></i>Import From Google Plus</a>
                    </li>
                  </ul>
                </div>
                <div class="alt-option">
                  <span>Or</span>
                </div>
                <div class="form-group"><label> </label> <input ref="id" v-model="user.id" name="id" type="text" id="id" /></div>
                <div id="idcheck-result" class="mb-3 text-primary" v-show="this.user.id !== ''">{{ this.idMsg }}</div>
                <div class="form-group"><label></label> <input ref="pass" v-model="user.pass" name="pass" type="password" id="password" /></div>
                <div class="form-group"><label></label> <input ref="name" v-model="user.name" name="name" type="text" id="name" /></div>
                <div class="form-group"><label></label> <input ref="address" v-model="user.address" name="address" type="text" id="address" /></div>
                <div class="form-group"><label></label> <input ref="phone" v-model="user.phone" name="phone" type="text" id="phone" /></div>
                <div class="check-and-pass">
                  <div class="row align-items-center">
                    <div class="col-12">
                      <div class="form-check">
                        <input type="checkbox" class="form-check-input width-auto" id="exampleCheck1" />
                        <label class="form-check-label">  <a href="javascript:void(0)">Terms and Conditions</a> </label>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="button">
                  <button type="submit" class="btn" id="btn-join"> </button>
                </div>
              </form>
              <p class="outer-link">
                  ?
                <router-link :to="{ name: 'login' }"></router-link>
              </p>
            </div>
          </div>
        </div>
      </div>
    </section>
    <!--   -->

    <a href="https://demo.graygrids.com/themes/classigrids-demo/registration.jsp#" class="scroll-top btn-hover"> <i class="lni lni-chevron-up"></i> </a>
  </div>
</template>

<script>
import http from '@/api/http';
export default {
  data() {
    return {
      idValid: false,
      idMsg: '',
      user: {
        id: '',
        pass: '',
        name: '',
        address: '',
        phone: '',
      },
    };
  },
  //watch? computed? updated?   ? 
  watch: {
    'user.id': function (inputId) {
      let minLength = 5;
      let maxLength = 16;
      if (inputId.length < minLength || inputId.length > maxLength) {
        this.idMsg = ` ${minLength}  ${maxLength}  .`;
      } else {
        http.get(`user/idCheck?id=${this.user.id}`).then(({ data }) => {
          //data: userid 
          if (data == 0) {
            this.idValid = true;
            this.idMsg = inputId + '   .';
          } else {
            this.idMsg = inputId + '   .';
          }
        });
      }
    },
  },
  methods: {
    moveHandler() {
      this.$router.push({
        name: 'home',
      });
    },
    registHandler() {
      // 
      let err = false;
      let msg = '';
      !this.user.id && ((msg = '  '), (err = true), this.$refs.id.focus());
      !err && !this.idValid && ((msg = ' '), (err = true), this.$refs.id.focus());
      !err & !this.user.pass && ((msg = ' '), (err = true), this.$refs.pass.focus());
      !err & !this.user.name && ((msg = ' '), (err = true), this.$refs.name.focus());
      !err & !this.user.address && ((msg = ' '), (err = true), this.$refs.address.focus());
      !err & !this.user.phone && ((msg = ' '), (err = true), this.$refs.phone.focus());

      if (err) {
        alert(msg);
      } else {
        //
        http.post('/user', this.user).then(({ data }) => {
          if (data == 'success') {
            alert(' ');
            this.moveHandler();
          }
        });
        // .catch(({ data }) => {
        //   alert(data);
        // });
      }
    },
  },
};
</script>
